import styled from 'styled-components'

const H1 = styled.h1`

  font-size: 1.25rem;
  line-height: 1.5;
  text-align: center;
  position: relative;

  &:after{
    content: '...';
    position: absolute;
    color: white;
    bottom: 0;
    
  }

`

export default H1


